<template>
  <div id="app">
    <c-header></c-header>
    <c-aside
      class="aside"
      :isCollapse.sync="isCollapse"
      :style="{width:`${asideWidth}px`}"
      @asideChange="asideWidthChange"
      :menuList="menuList"
      :submenuList="'children'"
    />
    <c-main class="main" :asideWidth="asideWidth" v-model="tabId" ref="main"/>
  </div>
</template>
<script>
import Header from "@c/shell/Header";
import Aside from "@c/shell/Aside.1";
import Main from "@c/shell/Main.1";

export default {
  name: "AppHome",
  mounted() {
    /* this.$post("/proxy/save", {
      doc: { id: "FuNvBingPuCha", content: JSON.stringify(menuList) }
    }) */
    this.$post("/proxy/get", {
      coll: "menuList" /* collection */,
      doc: { id: "FuNvBingPuCha" }
    })
      .then(({ isSuccess, data }) => {
        console.log(data);
        if (isSuccess) {
          this.menuList = JSON.parse(data[0] && data[0].content);
        }
      })
      .catch(error => console.log("error", error));

    /* 初始化婚前医学检查模块的currentModel，一旦变化，该功能栏下所有模块数据都会相应变化*/
    /* 存储在localStorage的是基本类型字符串，需要使用JSON.parse转换后使用*/
    window.addEventListener("message", receiveMessage, false);
    function receiveMessage(event) {
      function takeAction({ data: { action, payload }, source, origin }) {
        console.log(action, payload, source, origin);
        const ACTIONS = {};
        ACTIONS[action] && ACTIONS[action](payload);
      }
      try {
        takeAction(event);
      } catch (error) {
        console.log(error);
      }
    }
  },

  data() {
    return {
      isCollapse: true,
      hjhszIframe: false,
      /*  */
      menuList: [],
      asideWidth: 200,
      tabId: "1"
      /*  */
    };
  },
  methods: {
    addTab(tab) {
      this.$refs.main.addTab(tab);
    },
    asideWidthChange(rect) {
      console.log(rect);
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  computed: {},
  watch: {
    asideWidth(newValue, oldValue) {
      console.log("asideWidth", newValue, oldValue);
    },
    hjhszIframe(newValue) {
      console.log("hjhszIframe", newValue);
    }
  },
  components: {
    CHeader: Header,
    CAside: Aside,
    CMain: Main
  }
};
</script>
